import {
  HELP_FAIL,
  HELP_SUCCESS,
  CREATEHELP_SUCCESS,
  CREATEHELP_FAIL,
} from "../actions/types";

const deafaultState = {};
deafaultState.helpsdata = null;
deafaultState.createhelpdata = null;

export default function (state = deafaultState, action) {
  const { type, payload } = action;
  switch (type) {
    case HELP_SUCCESS:
      return {
        ...state,
        helpsdata: payload.helpsdata,
      };
    case HELP_FAIL:
      return {
        ...state,
        helpsdata: [],
      };
    case CREATEHELP_SUCCESS:
      return {
        ...state,
        createhelpdata: payload.createhelpdata,
      };
    case CREATEHELP_FAIL:
      return {
        ...state,
        createhelpdata: [],
      };
    default:
      return state;
  }
}
